// bumper
import { useEffect, useState } from 'react';
import ctx, { mapData, unmapActions } from '../events';
import ExportOutlined from '@ant-design/icons/ExportOutlined';
import '../styles/common.scss';

const headerTextList = [
  'Date',
  'Action',
  'Description',
  'Collateral',
  'Paird Token',
  'Expiry',
  'Mint Radio'
]


const Row = (props) => {
  const { item } = props;
  const { date, action, description, col, pairedToken, expiry, mintRadio, hash } = item;
  const url = 'https://etherscan.io/tx/' + hash
  return (
    <tr>
      <td className="td">{date}</td>
      <td className="td" style={{width: '120px'}}>{action}
      <ExportOutlined className="icon" onClick={() => { window.open(url, '_blank') }}/>
      </td>
      <td className="td">{description}</td>
      <td className="td">{col}</td>
      <td className="td">{pairedToken}</td>
      <td className="td">{expiry}</td>
      <td className="td">{mintRadio}</td>
    </tr>
  )
}

const PageView = () => {
  const [totalStake, setTotalStake] = useState(0);
  const [apy, setApy] = useState(0);

  // INIT
  useEffect(() => {
    const lifetimeObj = {};

    mapData({
      ANOTotalStake(value) { setTotalStake(value); },
      pool1Apy(value) { setApy(value); }
    }, ctx, lifetimeObj);

    return () => {
      unmapActions(lifetimeObj);
    }
  }, []);

  const list = [{
    date: 'Mar 05, 2021 11:16 AM GMT+8(25 days ago',
    action: 'lend',
    description: 'loaned 4200 DAI for RC tokens',
    col: 'WBTC',
    pairedToken: 'DAI',
    expiry: 'Api 01 2021 11:16 AM GMT+8',
    mintRadio: '25000',
    hash: '0x42d3966bb60dc907851b04e98db647ab3b11aed07b3eb0da9f9d6761f448f2be'
  },
  {
    date: 'Mar 05, 2021 11:16 AM GMT+8(25 days ago',
    action: 'Borrow',
    description: 'loaned 4200 DAI for RC tokens',
    col: 'WBTC',
    pairedToken: 'DAI',
    expiry: 'Api 01 2021 11:16 AM GMT+8',
    mintRadio: '25000',
    hash: '0x42d3966bb60dc907851b04e98db647ab3b11aed07b3eb0da9f9d6761f448f2be'
  }]

  return (
    <div className="page-center history" style={{ justifyContent: 'flex-start' }}>
      <div className="container">
        <div className="bg">
          <div className="title">My History</div>
          <table className="table">
            <thead>
              <tr>
                {
                  headerTextList.map((text, i) =>
                    <th key={i}>{text}</th>
                  )
                }
              </tr>
            </thead>
            <tbody>
              {
                list.map((item, i) =>
                  <Row item={item} key={i} />
                )
              }
            </tbody>
          </table>
          {
            list.length ? null :
              <div className="empty tac">Interact with the protocol to view your history!</div>
          }
        </div>
      </div>
    </div>
  )
};

export default PageView;
